* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

:root {
    font-size: 16px;
    --bg: #002fb19d;
    --nav: rgb(27, 27, 27);
    --icon-primary: rgb(75, 250, 90);
    --icon-secondary: rgb(39, 104, 44);
    --nav-text: rgb(255, 255, 255);
    --nav-font: 'Znikomit';
}

.navbar {
    display: flex;
 }

.navbar-container {
    position: fixed;
    width: 100%;
    background-color: rgb(27, 27, 27);
    background-color: var(--nav);
    font-family: 'Monoton', cursive, 'sans-serif';  
    align-items: center;
    text-align: center;
    justify-content: center;
    transition: all 0.2s;
    z-index: 10;

}

.navbar-title {
    width: 100%;
    display: flex;
    padding: 5% 10%;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    transition: all 0.2s;
}

.navbar-title-text {
    display: none;
    color: rgb(75, 250, 90);
    color: var(--icon-primary);
    letter-spacing: 0.5rem;
    align-items: center;
    text-align: left;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-family: 'Persian';
}

.navbar-icon-main {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: rgb(75, 250, 90);
    color: var(--icon-primary);
    margin-left: auto;
    margin-right: auto;
    transform: rotate(0deg);
    transition: all 0.2s ease-in-out;

}

.navbar-container:hover .navbar-title-text,
.navbar-container:hover .navbar-text {
    display: flex;
}


.navbar-container:hover .navbar-icon-main {
    transform: rotate(180deg);
    filter: grayscale(100%) opacity(0.8); 
}

.navbar-container:hover .navbar-title {
    border-bottom: 0.05rem solid white;
    
}

.navbar-item {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;   
    transition: all 0.2s ease-in-out;
    text-transform: uppercase;

}

.navbar-link:hover .navbar-icon-secondary {
    filter: grayscale(0%) opacity(1);
    
}
.navbar-link:hover .navbar-text {
    filter: grayscale(0%) opacity(1);
}

.navbar-icon-primary {
    color: rgb(75, 250, 90);
    color: var(--icon-primary);
    filter: grayscale(100%) opacity(0.5);
    transition: all 0.2s ease-in-out;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;   
    
}

.navbar-text {
    color: rgb(255, 255, 255);
    color: var(--nav-text);
    filter: grayscale(100%) opacity(0.5);
    display: none;
    align-items: center;
    text-align: center;
    justify-content: center;   
    transition: all 0.2s ease-in-out;
    align-self: flex-start;
    font-family: 'Znikomit';
    font-family: var(--nav-font);
}

.navbar-link {
    display: flex;
    align-items: center; 
    justify-content: center;
    text-decoration: none;
    background-color: rgb(27, 27, 27);
    background-color: var(--nav);
    transition: all 0.2s ease-in-out;
}

.link {
    display: flex;
    text-decoration: none;
    align-items: center; 
    justify-content: center;
    transition: all 0.2s ease-in-out;

}





/*media queries*/

/*medium size screen*/

@media (min-width: 800px) {
    .navbar {
        flex-direction: column;
        z-index: 10;
     }
     
     .navbar-container {
        top: 0;
        left: 0;
        display: flex;
        position: fixed;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        height: 100%;
        width: 5rem;

    }

    .navbar-container:hover{
        width: 320px;
            
    }

    .navbar-item {
        display: flex;
        flex-direction: column;
        height: 90%;
        width: 100%;
        align-items: center;
        text-align: center;
        justify-content: center;
        font-size: 2rem;
        letter-spacing: 0.125em;
    }

    .navbar-title {
        font-size: 1.2rem;
        width: 100%;
        
    }
    .navbar-icon-main {
        display: flex;
        flex-direction: column;
        
        
    }
    .navbar-link{
        text-align: center;
        justify-content: center;
        align-items: center;
        height: 15%;
        width: 100%;
    }

    .link {
        width: 80%;
        height: 100%;
        display: flex;
        justify-content: center;
        text-align: center;
        align-items: center;
        grid-gap: 50px;
        gap: 50px;
        font-size: 1.8rem;
        font-weight: 500;
    }

    .navbar-link:last-child {
        margin-top: auto;
    }

    .navbar-icon-primary {
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
        width: 100%; 

    }

    .navbar-text {
        align-items: center;
        text-align: left;
        justify-content: flex-start;
        height: 100%;
        width: 100%;
    }

}

/*small size screen*/

@media screen and (max-width: 800px) {
   
    .navbar{
        flex-direction: row;
        align-items: center;
        justify-content: center;

    }

    .navbar-container {
        bottom: 0;
        display: flex;
        flex-direction: row;
        height: 5rem;
        width: 100%;
        text-align: center;
        align-items: center;
        justify-content: center;
    }

    .navbar-title,
    .navbar-icon-main {
        display: none;

    }


    .navbar-item {
        display: flex;
        flex-direction: row;
        width: 100%;
        text-align: center;
        align-items: center;
        justify-content: space-around;
        letter-spacing: 0.25em;
        
    }


    .navbar-text {
        display: none;
        text-align: center;
        align-items: center;
        justify-content: center;
        align-self: center;
        height: 50%;
    }

    

    .navbar-link{
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        flex: 1 1;

    }

    .link {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        font-size: 1rem;
        letter-spacing: 0.5px;

    }

    .navbar-icon-primary {
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: center;
        font-size: 3em;
    }

}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Old Standard TT', serif, sans-serif;
  
}

:root {
  --name-font: 'Persian', serif;
  --typewriter: rgb(189, 189, 189);
  --text-c: rgb(218, 218, 218);
  --text: rgb(202, 202, 202);
  --bg-home: rgb(0, 0, 0);
  --white: rgb(255, 255, 255);
  --black: rgb(0, 0, 0);
  --card-color: rgb(255, 241, 52);
  --green: rgb(0, 128, 0);
  --shadow: rgba(31, 31, 31, 0.5);
  --header: rgba(47, 131, 22, 0.5);
  --nav-width: 5rem;
  --nav-height: 5rem;
  --btn-resume: linear-gradient(
    90deg,
    rgba(0, 35, 36, 1) 0%,
    rgb(0, 172, 29) 48%,
    rgb(0, 88, 15) 100%
  );
  font-size: 16px;
}
.container {
  background: radial-gradient(rgba(6, 71, 0, 0.5), rgb(0, 0, 0));
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
}

.home-container {
  background-color: rgb(0, 0, 0);
  background-color: var(--bg-home);
}

.home-header {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  text-shadow: 2px 2px 5px #000;
  color: rgb(218, 218, 218);
  color: var(--text-c);
}

.avatar {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.mid-section {
  height: 100vh;
  width: 100%;
  background: url(/portfolio/static/media/vaporwave-38.cf2c6d1a.jpg) no-repeat center center;
  background-size: cover;
}

.caret {
  z-index: 2;
  position: absolute;
  display: flex;
  margin-right: auto;
  margin-left: auto;
}
.typewriter {
  z-index: 2;
}

.home-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.btn-resume {
  justify-content: center;
  align-items: center;
  text-align: center;
  text-decoration: none;
  background: linear-gradient(
    90deg,
    rgba(0, 35, 36, 1) 0%,
    rgb(0, 172, 29) 48%,
    rgb(0, 88, 15) 100%
  );
  background: var(--btn-resume);
  box-shadow: 0px 4px 8px rgba(134, 151, 168, 0.1);
  border-radius: 15px;
  bottom: none;
}
.btn-resume:hover {
  letter-spacing: 1px;
  transform: scale(1.1);
}
.bio {
  background: radial-gradient(circle at 20% 20%,rgba(0, 128, 0, 0.3), rgba(0, 0, 0, 0.2));
}
