  text-decoration: none;
  height: 100%;
  width: 100%;
  min-height: 240px;
  min-width: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.card-link:hover {
  cursor: default;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  box-shadow: -1rem 0 3rem #000;
  border-radius: 16px !important;
  transition: all 0.2s;
  background-color: var(--card-color) !important;
  cursor: auto;
}

.card:hover {
  transform: translateY(-1rem);
}

.card:hover ~ .card {
  transform: translateX(130px);
}

.card:not(:first-child) {
  margin-left: -130px;
}

.card-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: transparent !important;
  width: 90%;
  grid-gap: 50px;
  gap: 50px;
  font-weight: bold;
  transition: all 0.1s;
  text-shadow: 5px 5px 2px 0px rgba(0, 0, 0, 0.5);


}

.card:hover .card-header {
  /* fill: linear-gradient(
