  font-size: 1.2rem;
  font-weight: bold;
  text-shadow: 8px 8px 2px 0px rgba(0, 0, 0, 1);
}

@media screen and (max-width: 768px) {
  .card:hover ~ .card {
    transform: translateX(80px);
  }

  .card:not(:first-child) {
    margin-left: -80px;
  }
}

